.visitors {
  margin: 0 auto;
  max-width: 90%;
}

.visitors__divisor {
  background-position: center;
  background-image: url(../../images/pessoasDancando.jpg);
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 447px;
}

.visitors__end-image {
  margin: 0 auto;
  width: 100%;
  height: 350px;
  background: url(../../images/dancandoSemFundo.png);
  background-size: cover;
}

@media screen and (min-width: 767px) {
  .visitors__end-image {
    max-width: 64rem;
    background-position: top;
  }
}